.header {
    height: 70px;
    width: 100%;
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
  
    .logo-container {
      height: 100%;
      width: 70px;
      padding: 25px;
    }
  
    .options {
      width: 50%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
  
      .option {
        padding: 10px 15px;
        cursor: pointer;
      }
    }

    .currentUserDisplayName{
      position: absolute;
      right: 15px;
      top: -20px;

      p{
        max-width: 250px;
        overflow: hidden;
      }
    }
  }
  